    //       () => {
    //         suspiciousAmountIntervalDistributionOptions.grid3D.viewControl.autoRotate = true;
    //       }
    //     );
    //   }
    // });

    const suspiciousTypeRatioChart = ref();

    const suspiciousCasesOptions = reactive({
      color: ["#407fff", "#04ba19", "#e15d68", "#f29961", "#00cccd"],
      backgroundColor: "transparent",
      grid: {
        left: "12%",
        top: "5%",
        bottom: "12%",
        right: "8%",
      },
      xAxis: {
        data: [
          "医院1",
          "医院2",
          "医院3",
          "医院4",
          "医院5",
          "医院6",
          "医院7",
          "医院8",
          "医院9",
          "医院10",
        ],
        axisTick: {
          show: false,
        },
        axisLine: {
          lineStyle: {
            color: "rgba(255, 129, 109,.1)",
            width: 1, //这里是为了突出显示加上的
          },
        },
        axisLabel: {
          textStyle: {
            color: "#00ffff",
            fontSize: 25,
          },
        },
      },
      yAxis: [
        {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "rgba(255, 129, 109, 0.1)",
              width: 1, //这里是为了突出显示加上的
            },
          },
          axisLabel: {
            textStyle: {
              color: "#00ffff",
              fontSize: 25,
            },
          },
          splitArea: {
            areaStyle: {
              color: "rgba(255,255,255,.5)",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(255, 129, 109, 0.1)",
              width: 0.5,
              type: "dashed",
            },
          },
        },
      ],
      series: [
        {
          type: "pictorialBar",
          barCategoryGap: "0%",
          symbol: "path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z",
          label: {
            show: true,
            position: "top",
            distance: 15,
            color: "#08DFFE",
            fontWeight: "bolder",
            fontSize: 25,
          },
          itemStyle: {
            color: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: "#9A11FF",
                },
                {
                  offset: 1,
                  color: "#08DFFE",
                },
              ],
              global: false, //  缺省为  false
            },
          },
          emphasis: {
            itemStyle: {
              opacity: 1,
              color: "#ff0000",
            },
          },
          data: [123, 60, 25, 18, 12, 9, 2, 1, 5, 6],
        },
        {
          type: "pie",
          center: ["75%", "25%"],
          radius: ["15%", "30%"],
          roseType: true,
          itemStyle: {
            shadowBlur: 20,
            shadowColor: "rgba(91, 12, 10, 0.5)",
          },
          label: {
            formatter(params) {
              return `{a|${params.name}}\n\n{b|${params.value}%}`;
            },
            rich: {
              a: {
                fontSize: 30,
                color: "#00ffff",
              },
              b: {
                fontSize: 25,
                color: "#e15d68",
              },
            },
          },
          labelLine: {
            lineStyle: {
              color: "#00ffff",
              width: 2,
            },
          },
          data: [
            {
              name: "重叠住院",
              value: 25,
            },
            {
              name: "项目异常1",
              value: 30,
            },
            {
              name: "项目异常2",
              value: 40,
            },
            {
              name: "项目异常3",
              value: 10,
            },
            {
              name: "项目异常4",
              value: 60,
            },
          ],
        },
      ],
    });

    let i = 0;
    setInterval(() => {
      if (
        suspiciousTypeRatioChart.value &&
        suspiciousTypeRatioChart.value.chart
      ) {
        suspiciousTypeRatioChart.value.chart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
        });
        suspiciousTypeRatioChart.value.chart.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex:
            i > suspiciousCasesOptions.series[0].data.length - 1
              ? (i = 0)
              : i++,
        });
      }
      suspiciousCasesOptions.series[1].data[0].value = Math.ceil(
        Math.random() * 1000
      );
      suspiciousCasesOptions.series[1].data[1].value = Math.ceil(
        Math.random() * 1000
      );
      suspiciousCasesOptions.series[1].data[2].value = Math.ceil(
        Math.random() * 1000
      );
      suspiciousCasesOptions.series[1].data[3].value = Math.ceil(
        Math.random() * 1000
      );
      suspiciousCasesOptions.series[1].data[4].value = Math.ceil(
        Math.random() * 1000
      );
    }, 4000);

    return {
      suspiciousTypeOfDistrictAndCountyOptions,
      reimbursementAmountInvolvedOptions,
      suspiciousHospitalGradeOptions,
      suspiciousTypeRatioOptions,
      quota1Data1,
      quotasTitleStyle,
      quotasDataStyle,
      quotasSuffixStyle,
      config,
      table,
      suspiciousAmountIntervalDistributionChart,
      suspiciousAmountIntervalDistributionOptions,
      suspiciousCasesOptions,
      suspiciousTypeRatioChart,
    };
  },
};
</script>

