<template>
  <div class="quota3">
    <div class="icon"><img v-if="icon" :src="icon" :style="imageStyle" /></div>
    <div class="quota">
      <div class="data" v-if="data">
        <count-to
          class="digital"
          :startVal="prevData"
          :endVal="data"
          :style="dataStyle"
        />
        <span class="unit" :style="suffixStyle">{{ suffix }}</span>
      </div>
      <div class="title" v-if="title" :style="titleStyle">{{ title }}</div>
    </div>
  </div>
</template>

<script>
import { ref, watch } from "vue";

export default {
  props: {
    icon: {
      type: String,
      default: null,
    },
    imageStyle: {
      type: Object,
      default() {
        return {
          width: "6rem",
        };
      },
    },
    title: {
      type: String,
      default: null,
    },
    titleStyle: {
      type: Object,
      default() {
        return {
          color: "#50b4f2",
          fontSize: "1.5rem",
        };
      },
    },
    data: {
      type: Number,
      default: null,
    },
    dataStyle: {
      type: Object,
      default() {
        return {
          color: "#b5fd6e",
          fontSize: "2rem",
          fontWeight: "bold",
        };
      },
    },
    suffix: {
      type: String,
      default: "",
    },
    suffixStyle: {
      default() {
        return {
          color: "#b5fd6e",
          fontSize: "1.3rem",
        };
      },
    },
  },
  setup(props) {
    const prevData = ref(0);
    watch(
      () => props.data,
      (nextValue, prevValue) => {
        prevData.value = prevValue;
      }
    );

    return {
      prevData,
    };
  },
};
</script>

<style lang="scss" scoped>
.quota3 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  .icon {
    margin-right: 1rem;
  }
  .quota {
    display: flex;
    flex-direction: column;
    .title {
      margin-bottom: 1rem;
    }
    .data {
      .digital {
        font-family: LCD;
      }
    }
  }
}
</style>